import React from "react";
import Cloud from "./Cloud.jsx";
import Hill from "./Hill.jsx";
import Building from "./Building.jsx";
import BuildingBkg from "./Building_bkg.jsx";
import Maproad from "./Map_road.jsx";
import Mapland from "./Map_land.jsx";
import LogoGroup from "./logo.js";
import SurveyGrid from "./SurveyGrid.jsx";
import { Parallax, ParallaxLayer } from "@react-spring/parallax";
import { ReactComponent as Standing19 } from "./assets/art/svg/human/standing-19.svg";
import { ReactComponent as Sitting4 } from "./assets/art/svg/human/sitting-4.svg";
const questions = {
  age: {
    id: 0,
    Q: "What's your age group?",
    A: [
      { id: "0", label: "Over 65" },
      { id: "1", label: "Less than 65" }
    ]
  },
  location: {
    id: 1,
    Q: "Where do you live?",
    A: [
      { id: "0", label: "South Australia" },
      { id: "1", label: "Other state" }
    ]
  }
};

export default class Para extends React.Component {
  constructor(props) {
    super(props);
    this.scroll = this.scroll.bind(this);
    this.state = { result: {} };
  }

  scroll = (pageNumber) => {
    this.parallax.scrollTo(pageNumber);
  };
  componentDidMount() {
    this.props.onRef && this.props.onRef(this);
  }
  handleAnswer(Qid, answer) {
    let resultCopy = JSON.parse(JSON.stringify(this.state.result));
    resultCopy[Qid] = answer;
    this.setState({ result: resultCopy });
    console.log(this.state.result);
  }
  render() {
    const humanStyle = {
      height: "45vh"
    };
    const cloudRightLeftEnd = "80%";
    const cloudLeftLeftEnd =
      Number((document.body.clientWidth > 500 ? 0.15 : -0.05) * 100).toFixed(
        1
      ) + "%";

    return (
      //  {Object.keys(this.state.result).map((id) => (
      //   <span key={id + "chishi"}>{id + " " + this.state.result[id]}</span>
      // ))}
      <Parallax ref={(ref) => (this.parallax = ref)} pages={4}>
        <ParallaxLayer
          offset={0}
          speed={1}
          style={{
            backgroundImage:
              "linear-gradient(-180deg, #5fb6d9 0%, #ededdf 100%)"
          }}
        />

        <ParallaxLayer
          offset={2}
          speed={1}
          style={{
            backgroundImage:
              "linear-gradient(-180deg, #63a7cf  0%, #458c4b 100%)"
          }}
        />

        <ParallaxLayer
          offset={1.999999}
          speed={1}
          style={{
            backgroundImage:
              "linear-gradient(-180deg, #fde3ff  0%, #63a7cf 100%)"
          }}
        />
        <ParallaxLayer offset={2} speed={-3}>
          <Mapland height={window.screen.height} />
        </ParallaxLayer>
        <ParallaxLayer offset={2} speed={2}>
          <Maproad height={window.screen.height} />
        </ParallaxLayer>
        <ParallaxLayer
          offset={0.999999}
          speed={1}
          style={{
            backgroundImage:
              "linear-gradient(-180deg, #ededdf 0%, #ededdf 100%)"
          }}
        />
        <ParallaxLayer
          offset={1}
          speed={1}
          style={{
            backgroundImage:
              "linear-gradient(-180deg, #ededdf 0%, #fde3ff 100%)"
          }}
        />
        <ParallaxLayer offset={1} speed={-0.2}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            version="1.1"
            viewBox="0 0 1080 500"
          >
            <circle cx="340" cy="100" r="40" fill="#d49c19" />
          </svg>
        </ParallaxLayer>
        <ParallaxLayer offset={1.5} speed={-2.8}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            version="1.1"
            viewBox="0 0 1920 1080"
          >
            <circle cx="300" cy="480" r="400" fill="#729466" />
          </svg>
        </ParallaxLayer>
        <ParallaxLayer offset={0} speed={1.2}>
          <Cloud width="15%" left={cloudLeftLeftEnd} opacity="0.5" />
        </ParallaxLayer>
        <ParallaxLayer offset={0.1} speed={1.2}>
          <Cloud width="10%" left={cloudRightLeftEnd} opacity="0.5" />
        </ParallaxLayer>

        <ParallaxLayer offset={0.2} speed={-2.3}>
          <Hill height={window.screen.height} />
        </ParallaxLayer>
        <ParallaxLayer offset={0.45} speed={-2.5}>
          <BuildingBkg height={window.screen.height} />
        </ParallaxLayer>
        <ParallaxLayer offset={0.55} speed={-2.7}>
          <Building height={window.screen.height} />
        </ParallaxLayer>
        <ParallaxLayer offset={1} speed={1}>
          <SurveyGrid
            Q={questions["age"]["Q"]}
            A={questions["age"]["A"]}
            id={questions["age"]["id"]}
            handleAnswer={this.handleAnswer.bind(this)}
            scroll={() => {
              this.scroll(2);
            }}
          >
            <Standing19 style={humanStyle} />
          </SurveyGrid>
        </ParallaxLayer>

        <ParallaxLayer offset={2} speed={1}>
          <SurveyGrid
            Q={questions["location"]["Q"]}
            A={questions["location"]["A"]}
            id={questions["location"]["id"]}
            handleAnswer={this.handleAnswer.bind(this)}
            scroll={() => {
              this.scroll(3);
            }}
          >
            <Sitting4 style={humanStyle} />
          </SurveyGrid>
        </ParallaxLayer>
        <ParallaxLayer
          offset={0.15}
          speed={0.3}
          onClick={() => this.scroll(1)}
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "center"
          }}
        >
          <LogoGroup />
        </ParallaxLayer>
      </Parallax>
    );
  }
}
